<template>
    <div class="message">
      <div>
        <span>{{error}}</span>
        <p>请稍后再试</p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ErrorMessage",
      props:["error"],
    }
</script>

<style scoped lang="stylus">
.message
  position: fixed
  top: 0
  left 0
  width: 100%
  height: 100%
  z-index 999
  div
    text-align center
    width: 3rem
    margin 70% auto
    background-color: rgba(0,0,0,.6)
    border-radius .12rem
    padding .2rem 0
    color #fff
    font-size .32rem
    font-weight 500
    p
      width: 2rem
      margin-top .1rem
      display inline-block
</style>



