<template>
    <div class="right">
      <h5>{{name.title}}</h5>
      <div class="bottom">
        <div class="box" v-for="newbox in name.newboxs" :key="newbox.id" @click="onClick(newbox.di)">
          <router-link :to="{path:'/attractions/' + newbox.di, query:{di: newbox.di}}">
            <div>
              <img :src="newbox.img" alt="">
            </div>
          </router-link>
          <p>{{newbox.di}}</p>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Right",
      data () {
          return {
            title:"",
          }
      },
      props:{
          name:{
            default:function () {
              return ['默认','默认']
            }
          }
      },
      methods:{
        onClick:(e) => {
          //console.log(e)
        }
      }
    }
</script>

<style scoped lang="stylus">
.right
  width: auto
  background-color: #fff
  padding-left .24rem
  overflow: hidden
  h5
    font-size .28rem
    color #9e9e9e
    margin-top .3rem
    display block
    height: .28rem
  .bottom
    width auto
    display flex
    flex-wrap wrap
    .box
      margin-top .25rem
      width: 1.62rem
      text-align center
      margin-right .2rem
      div
        border-radius .12rem
        overflow hidden
        width: 1.62rem
        height: 1.62rem
        img
          width: 100%
          height: 100%
      p
        color #3e3e3e
        margin-top .14rem
        font-size .26rem
</style>



