<script>
    export default {
        name: "HomeHeader",
    }
</script>

<style scoped lang="stylus">
.header
  padding .15rem .24rem
  overflow hidden
  display flex
  height .64rem
  background-color: #ffc900
  .left
    float left
    flex 1
    display flex
    overflow hidden
    line-height .64rem
    .l-left
      padding-right .28rem
      color #3f3e3c
      float left
      text-align center
      span
        font-size .44rem
        text-align center
    .l-right
      background-color: rgba(255,255,255,.9)
      color #3f3e3c
      padding 0 .14rem 0 .24rem
      float left
      flex 1
      border-top-left-radius .32rem
      border-bottom-left-radius .32rem
      border-bottom-right-radius .32rem
      display flex
      justify-content space-between
      span
        font-size .28rem
        margin-right .05rem
        color: #666
      .icon-yuyin
        font-size .38rem
      input
        flex 1
        margin-left .05rem
        background: none
  .right
    float right
    margin-left .18rem
    color #3f3e3c
    line-height .64rem
    span
      font-size .44rem
</style>



