<template>
    <div class="banner" :style="{backgroundImage:'url('+ name.imageUrls[0] +')', backgroundSize:'100%'}">
      <div class="txt">
        <div>
          <h5>{{name.title}}</h5>
          <p>上海</p>
          <span>景点人气第一名</span>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "banner",
      props:["name"],
    }
</script>

<style scoped lang="stylus">
.banner
  width: 100%
  height: 3rem
  overflow: hidden
  background: url("https://img.alicdn.com/tfscom/TB1U1VZeJzJ8KJjSspkSuvF7VXa_750x388q80.jpg")
  background-position: center center
  background-repeat: no-repeat
  .txt
    width: 100%
    height: 100%
    background: -webkit-gradient(linear,0 0,0 bottom,from(transparent),to(rgba(0,0,0,.6)))
    color #fff
    position: relative
    div
      position: absolute
      left .24rem
      bottom .24rem
    h5
      font-size .4rem
      font-weight 500
    p
      font-size .28rem
      margin-top .18rem
    span
      display inline-block
      margin-top .16rem
      font-size .24rem
      padding .05rem .08rem
      border-bottom-right-radius .15rem
      border-bottom-left-radius .15rem
      border-top-left-radius .15rem
      background: linear-gradient(135deg,#ff738b,#f20096)
</style>



