<template>
    <div class="attractions">
      <Message v-bind:error="error" v-show="error"></Message>
      <Header v-bind:town="this.$route.params.name"></Header>
      <List v-for="list in lists" v-bind:name="list" :key="list.id"></List>
      <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
        <Loading v-if="busy"></Loading>
      </div>
    </div>
</template>

<script>
import Header from "../Place/PlaceComponents/Header"
import List from "./attractionList"
import Loading from "../loading"
import Message from "../ErrorMessage"

var root = process.env.API_HOST

    export default {
        name: "Attractions",
      data () {
        return {
          di:"",
          lists:[],
          busy:false,
          page:1,
          error:"",
        }
      },
      components:{
          Header,
        List,
        Loading,
        Message,
      },
      methods:{
        loadMore: function () {
          this.busy = true;
          //官方示例中延迟了1秒，防止滚动条滚动时的频繁请求数据
          setTimeout(() => {
            this.qinqiu();
          },500)
        },
        qinqiu:function () {
          this.axios.get(root + "/sight/feizhu?", {
            params:{
              kw:this.$route.query.di,
              pageToken:this.page,
            }
          })
            .then((data) => {
              for(let i=0; i<data.data.data.length; i++){
                this.lists.push(data.data.data[i])
              }
              console.log(data);
              this.busy = false
              this.page ++
            })
            
        },
      },
      created:function(){
        //this.qinqiu();
      },
    }
</script>

<style scoped lang="stylus">
.attractions
  height: 100%
  position: relative
</style>



