<template>
    <div class="left">
      <ul>
        <li v-for="(cheng,index) of chengshi" @click="active(index)" :class="timeIndex === index ? 'active' : ''">{{cheng}}</li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "Left",
      data () {
          return {
            chengshi:["推荐", "中国", "亚洲", "欧洲", "北美洲", "大洋洲", "非洲", "南美洲", "南极洲",],
            timeIndex:0,
          }
      },
      methods:{
          active(index){
            this.timeIndex = index;
            //console.log(this.timeIndex)
          }
      }
    }
</script>

<style scoped lang="stylus">
.left
  width: 1.78rem
  ul
    overflow: hidden
    li
      line-height .88rem
      color #6e6e6e
      height .88rem
      display: inline-block
      width: 100%
      padding-left .24rem
      position: relative
    .active
      background-color: #fff
      color #faa429
      &:after
        content ""
        width: .04rem
        height: .36rem
        position: absolute
        top .25rem
        left 0
        border-radius .24rem
        background-color: #fed430
</style>



