<template>
    <div class="list">
      <router-link :to="{name:'Details', params:{name}}">
        <div class="main">
          <div class="left">
            <img :src="name.imageUrls" alt="">
          </div>
          <div class="right">
            <h5>{{name.title}}</h5>
            <span>{{name.openingHours}}</span>
            <p>{{name.description}}</p>
            <div><span>{{name.rating}}</span>分({{name.commentCount}}条点评)</div>
          </div>
        </div>
      </router-link>
    </div>
</template>

<script>
    export default {
        name: "attractionList",
      props:{
          name:{
            default:function () {
              return ['默认','默认']
            }
          }
      },
    }
</script>

<style scoped lang="stylus">
.list
  padding .24rem
  border-bottom .02rem solid #f3f3f3
  .main
    display: flex
    .left
      width: 2.2rem
      height: 1.64rem
      overflow: hidden
      border-radius .12rem
      img
        height: 100%
        min-width 2.2rem
    .right
      padding-left .24rem
      width: 4.55rem
      overflow hidden
      h5
        font-size .32rem
        line-height .48rem
        color #444
        overflow hidden
        white-space nowrap
        text-overflow ellipsis
      span
        display inline
        font-size .24rem
        color #999
        line-height .36rem
        overflow hidden
        white-space nowrap
        text-overflow ellipsis
      p
        font-size .26rem
        color #888
        line-height .38rem
        overflow:hidden;
        white-space nowrap
        text-overflow ellipsis
      div
        font-size .24rem
        color #666
        margin-top .05rem
        span
          font-weight 500
          color #ff5000
</style>



