<template>
    <div class="details">
      <Header v-bind:town="name.title"></Header>
      <Banner v-bind:name="this.$route.params.name"></Banner>
      <txt-main v-bind:name="name"></txt-main>
    </div>
</template>

<script>
import Header from "../Place/PlaceComponents/Header"
import Banner from "./DetailsComponents/banner"
import TxtMain from "./DetailsComponents/TextMain"

    export default {
        name: "Details",
      data () {
          return {
            name:{},
          }
      },
      components:{
        Header,
        Banner,
        TxtMain,
      },
      mounted:function () {
        this.name = this.$route.params.name
      }
    }
</script>

<style scoped lang="stylus">

</style>



