<template>
    <div class="home-swiper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="item of swiperList" :key="item.id">
          <img :src="item.imgURL" alt="">
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
      <div class="border-bj"></div>
    </div>
</template>

<script>
    export default {
        name: "HomeSwiper",
      data (){
          return {
            swiperOption:{
              pagination:'.swiper-pagination',    //分页，多个分页可以使用不同的class名
              loop: true,    //复制slide，循环滚动
              autoplay: 5000,   //自动播放间隔时间
              speed: 1000,    //切换速度，即slider自动滑动开始到结束的时间（单位ms），也是触摸滑动时释放至贴合的时间。
              paginationClickable: true,    //点击切换
              autoplayDisableOnInteraction:false,   //用户操作swiper之后，是否禁止autoplay.默认为true：停止。
              preventLinksPropagation:true     //拖动释放时不会输出信息，阻止click冒泡。拖动Swiper时阻止click事件。
            },
            swiperList:[{
              id:1,
              imgURL:"https://gw.alicdn.com/tfs/TB1Q3FDEXuWBuNjSszbXXcS7FXa-1125-352.jpg_960x960q90.jpg"
            },{
              id:2,
              imgURL:"https://gw.alicdn.com/tfs/TB1jTzcbxjaK1RjSZKzXXXVwXXa-1125-352.jpg_960x960q90.jpg"
            },{
              id:3,
              imgURL:"https://gw.alicdn.com/tfs/TB1pxbkbSzqK1RjSZFjXXblCFXa-1125-352.jpg_960x960q90.jpg"
            },{
              id:4,
              imgURL:"https://gw.alicdn.com/imgextra/i2/TB2yCO0X4YaK1RjSZFnXXa80pXa_!!0-travel.jpg_960x960q90.jpg"
            },]
          }
      }
    }
</script>

<style scoped lang="stylus">
.home-swiper >>> .swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets
  bottom: .1rem
.home-swiper >>> .swiper-pagination-bullet
  background-color: #fff
.home-swiper >>> .swiper-pagination-bullet-active
  background-color: #ffc900
img{
  width 100%
}
.home-swiper
  width: 100%
  height 0
  padding-bottom 31.2%
  background-color: #ccc
  position: relative
  .border-bj
    width: 100%
    height 10px;
    background: url("https://gw.alicdn.com/tfs/TB1BCCPbntYBeNjy1XdXXXXyVXa-750-12.png") no-repeat bottom center
    -webkit-background-size: 100%
    background-size: 100%
    position: absolute
    bottom -1px
    z-index 999
</style>



