        <div class="t-bottom">
          <div class="text">
            <div>推荐</div>
            <span>初探土耳其，穿梭古迹不停歇</span>
            <p>4人说好</p>
          </div>
          <div class="img"></div>
        </div>
      </div>
      <div class="bottom">
        <div class="box" v-for="texts in text">
          <div>
            <img :src="texts.img" alt="">
            <span>{{texts.place}}</span>
          </div>
          <p>{{texts.txt}}</p>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Global",
      data () {
          return {
            text:[{
              img:"https://gw.alicdn.com/imgextra/i3/6000000004600/TB2CtiIuhRDOuFjSZFzXXcIipXa_!!0-travel.jpg_400x400q75.jpg",
