              img:"https://gw.alicdn.com/imgextra/i1/6000000004074/TB2n4JNXOHmJKJjy0FfXXb9aVXa_!!0-travel.jpg_400x400q75.jpg",
              place:"尼泊尔",
              txt:"追寻神的国度，探秘尼泊尔文明"
            },]
          }
      }
    }
</script>

<style scoped lang="stylus">
.global
  width: 100%
  .top
    .title
      position: relative
      img
        width: 100%
      p
        color #686868
        position: absolute
        top: .42rem
        right .6rem
    .t-bottom
      margin 0 .24rem
      background-color: #f2f3f4
      height: 2.56rem
      display flex
      justify-content space-between
      padding-left .12rem
      .text
        width: 1rem
        div
          font-size .24rem
          background-color: #ffc900
          color #3d3d3d
          display inline-block
          padding .1rem
        span
