          display inline-block
          margin-top .22rem
          color #454545
          line-height .42rem
        p
          font-size .24rem
          color #a6a6a6
          margin-top .26rem
          position: relative
          &:after
            content ""
            width: .6rem
            height: .02rem
            background: #f7cb41
            position: absolute
            top: -.14rem
            left: 0
    .img
      width: 4.64rem
      background: url("https://gw.alicdn.com/imgextra/i2/6000000005645/TB2pynmhbsTMeJjy1zbXXchlVXa_!!0-travel.jpg_500x500q75.jpg") no-repeat
