      background-size: 100% 100%
  .bottom
    margin .12rem .24rem 0
    padding-bottom .34rem
    border-bottom 0.04rem solid #f6f6f6
    display flex
    justify-content space-between
    .box
      width: 2.26rem
      div
        width: 100%
        height: 1.28rem
        position: relative
        img
          width: 100%
          height: 100%
        span
          font-size .24rem
          color #fff
          padding .1rem .12rem
          background-color: rgba(0,0,0,.6)
          position: absolute
          bottom: 0
          left: 0
      p
        margin-top .16rem
        line-height .36rem
        font-size .24rem
        color #333
</style>



