<template>
    <div class="text">
      <div class="top">
        <div class="pinfen">
          <div><span>{{name.rating}}</span>分</div>
          <div><span>{{name.commentCount}}</span>条点评<span class="iconfont icon-jinrujiantou"></span></div>
        </div>
        <div class="dizhi">
          <div><span>地址</span>上海市浦东新区申迪路753号</div>
          <div>
            <span class="iconfont icon-daohang"></span>
            <p>导航</p>
          </div>
        </div>
      </div>
      <div class="bottom">
        <div>
          <div v-bind:class="{'txts': txt}" class="txt">
            <h5>实用信息</h5>
            <span>{{name.content}}</span>
            <div>
              <span v-if="txt" @click="gaiTxt">收起</span>
              <span v-else="txt" @click="gaiTxt">展开</span>
            </div>
          </div>
        </div>
        <div class="lie2">
          <h5>营业时间</h5>
          <span>{{name.openingHours}}</span>
        </div>
        <div class="lie3">
          <h5>联系电话</h5>
          <span v-for="tetlp in name.telephones">{{tetlp}}</span>
        </div>
        <div class="lie4">
          <h5>交通</h5>
          <span>步行即可到达。</span>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "TextMain",
      data () {
          return {
            txt:false,
          }
      },
      props:["name"],
      methods:{
          gaiTxt:function () {
            this.txt = !this.txt
          }
      }
    }
</script>

<style scoped lang="stylus">
.text
  .top
    padding-left .24rem
    border-bottom .12rem solid #f2f3f4
    .pinfen
      display: flex
      justify-content space-between
      line-height .88rem
      border-bottom .02rem solid #f7f7f7
      & div:nth-child(1)
        span
          font-size .36rem
          font-weight bold
          color #ffc900
      & div:nth-child(2)
        color #333
        margin-right .24rem
        span:nth-child(1)
          font-size .3rem
          font-weight 500
    .dizhi
      display flex
      justify-content space-between
      color #444
      overflow: hidden
      div:nth-child(1)
        line-height 1.18rem
        span
          font-size .28rem
          font-weight bold
          padding-right .2rem
      div:nth-child(2)
        margin-right .24rem
        text-align center
        color #ff5000
        padding-top .3rem
        span
          font-size .4rem
        p
          font-size .24rem
  .bottom
    padding 0 .24rem
    color #444
    div
      h5
        font-size .28rem
        font-weight bold
        padding-right .1rem
        display inline-block
    div:nth-child(1)
      position: relative
      line-height .4rem
      padding .15rem 0
      border-bottom .02rem solid #f6f6f6
      overflow: hidden
      .txt
        position: relative
        padding 0
        border none
        overflow: hidden
        display:-webkit-box
        -webkit-line-clamp:2
        -webkit-box-orient:vertical
        white-space:normal
        span:nth-child(2)
          overflow: hidden
          height: .6rem
        div
          position: absolute
          right 0
          bottom 0
          background-image: linear-gradient(90deg,hsla(0,0%,100%,0),#fff 30%,#fff)
          span
            font-weight 400
            padding .1rem .1rem .1rem .4rem
            color #e90
      .txts
        -webkit-line-clamp: 1000
    .lie3
      span
        color #e90
        font-weight 500
        margin-right .2rem
    .lie2
    .lie3
    .lie4
      line-height  .8rem
      border-bottom .02rem solid #f6f6f6
    .lie2
      line-height .36rem
      padding .2rem 0
</style>



